// Import theming functions
@import '~@angular/material/theming';

.sidenav-container {
  height: 100%;
}

.sidenav {
  width: 200px;
}

.sidenav .mat-toolbar {
  background: inherit;
}

.mat-toolbar.mat-primary {
  position: sticky;
  top: 0;
  z-index: 1;
}

.hidden-router {
  flex: 1 1 auto;
  display: flex;
  overflow: hidden;
}

.toolbar-spacer {
  flex: 1 1 auto;
  display: flex;
}

.user-avatar {
  height: 42px;
  width: 42px;
  padding-right:10px;
  border-radius: 50%;
  flex-shrink: 0;
  object-fit: cover;
}

button.discord-login {
  background-size: 30px;
  background-position: 5px;
  background-repeat: no-repeat;
}


@mixin menuitem-active($theme) {
  $primary: map-get($theme, primary);
  $accent: map-get($theme, accent);

  .menuitem.active {
    background-color: mat-color($primary, darker);
    font-weight: 900;
  }
}
